import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react"
import { refreshQuery } from "./authService/refreshApiSlice"
import { setError } from "./errorService/errorSlice"
import { setAuthorized } from "./authService/authSlice"

export const baseQuery = fetchBaseQuery({
	baseUrl: "https://sidney.miridey.com/api/v1",
	prepareHeaders: (headers) => {
		const token = localStorage.getItem("accessToken")

		if (token) {
			headers.set("authorization", `Bearer ${token}`)
		}
		return headers
	}
})

const baseQueryWithReauth = async (args, api, extraOptions) => {
	const { dispatch } = api
	const access = localStorage.getItem("accessToken")
	const refresh = localStorage.getItem("refreshToken")
	const authorized = localStorage.getItem("authorized")
	// если вынести переменные, все сломается

	if (authorized && (!access || !refresh)) {
		dispatch(setAuthorized(false))
		localStorage.clear()
		localStorage.setItem("storage_wipe_reason", "no_token/s")
		console.log(localStorage)
		return
	}

	try {
		console.log(localStorage)

		let result = await baseQuery(args, api, extraOptions)
		console.log("basic api result", result)

		if (result?.error?.status !== 401) {
			dispatch(setError(result?.error))
		}

		if (result?.error?.status === 401) {
			const refreshResult = await refreshQuery("/token/refresh/", api, extraOptions)
			console.log("refreshResult", refreshResult)

			if (refreshResult?.data) {
				localStorage.setItem("accessToken", refreshResult?.data?.access)
				result = await baseQuery(args, api, extraOptions)
			} else {
				dispatch(setAuthorized(false))
				localStorage.clear()
				localStorage.setItem("storage_wipe_reason", "refresh_token_invalid")
			}
		}

		return result
	} catch (e) {
		console.log("basic api catch", e)
	}
}

export const apiSlice = createApi({
	baseQuery: baseQueryWithReauth,
	endpoints: builder => ({})
})