import { useTranslation } from "react-i18next"
import "./PopupError.css"
import { useDispatch, useSelector } from "react-redux"
import { setError } from "../../redux/errorService/errorSlice"

import Button from "../Button/Button"

const PopupError = () => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const error = useSelector(state => state.error.error)

  const sendReport = () => {
    dispatch(setError(false))
  }

  console.log(error)

  return (
    <div className="popup-error__container">
      <div className="popup-error__wrapper">
        <p className="popup-error__body">
          {error?.originalStatus === 500 && t("The server is not responding. Please try later")}
          {!error?.originalStatus && error && t("Internal error. Please try later")}
        </p>

        <Button onClick={sendReport} title={t("Report")} />
      </div>
    </div>
  )
}

export default PopupError